/* Side-By-Side Images */

// Note: The image needs a div with this class... class="img-container and class="responsive-img""

img {
  display: block;
  max-inline-size: 100%;
  // max-width: 30%; //was 100%
  // height: auto;
  // border-radius: 6px;
  // float: right;
  border: 5px solid black;
}

/* Default styles (large screens) */
.responsive-img {
    float: right;
    width: 20%; //was 100%
    height: auto;
    margin-left: 15px; /* Add some space between the image and text */
}

/* Styles for small screens */
@media (max-width: 600px) {
    .responsive-img {
        float: none; /* Remove the float */
        display: block; /* Change to block element for margin auto to work */
        margin: 0 auto; /* Center the image horizontally with auto margins */
        width: 75%; /* Optionally adjust width for the smaller screen */
        height: auto;
    }
    
    /* Ensure the parent container has text-align: center if you use inline-block centering */
    .img-container {
        text-align: center;
    }
}